.home-container {
  height: 100vh;
  width: 100%;
  background-image: url('/public/images/home-background.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-color: #f4f4f4;
  position: relative;
}

.top-menu {
  display: flex;
  justify-content: center;
  gap: 2rem;
  transform: translateX(-20px); /* ~1 pollice */
}



.top-menu a {
  text-decoration: none;
  font-size: 1.7rem;
  font-weight: 700;
  font-family: 'Tangerine', cursive;
  margin-top: 30px;
  color: #bfa046;
  position: relative;
  transition: color 0.4s ease-in-out;
}


.top-menu a::after {
  content: '';
  position: absolute;
  top: 0;
  left: -75%;
  width: 50%;
  height: 100%;
  background: linear-gradient(120deg, transparent, rgba(255,255,255,0.6), transparent);
  transform: skewX(-20deg);
  opacity: 0;
  transition: left 0.6s ease-in-out, opacity 0.4s;
}


.top-menu a:hover::after {
  left: 125%;
  opacity: 1;
}




.top-menu a:hover {
  color: #4d6f4f;
}

@media (max-width: 768px) {
  .home-container {
    background-size: cover;
    background-position: center;
    padding: 1rem;
    height: auto; /* Per evitare che tagli contenuto su schermi piccoli */
  }

  .top-menu {
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    transform: none;
    margin-bottom: 1.5rem;
  }

  .top-menu a {
    font-size: 1.4rem;
    margin-top: 0;
  }

  .home-content {
    text-align: center;
    padding: 0 1rem;
  }

  .home-title {
    font-size: 2rem;
  }

  .home-subtitle {
    font-size: 1.2rem;
  }

  .home-date,
  .home-location {
    font-size: 0.95rem;
    line-height: 1.4;
  }
}



